<script setup>
import NoNotification from '~/components/BaseCenter/CenterBody/NoNotification.vue';
import SectionList from '~/components/BaseCenter/CenterBody/SectionList.vue';
import CenterHeader from '~/components/BaseCenter/CenterHeader/CenterHeader.vue';
import { ref, computed, watch } from 'vue';
import { useInfiniteScroll, useScroll } from '@vueuse/core';
import useNotificationsStore from '~/stores/notifications';
import { useNotificationManagement } from '../../composables/notification/useNotificationManagement';

const { loadMoreNotifications } = useNotificationManagement();

// not destructuring the store to avoid reactivity issue
const store = useNotificationsStore();

// to get the scroller element
const scroller = ref(null);

/**
 * Indicates whether the store is currently loading.
 * @type {ComputedRef<boolean>}
 */
const loading = computed(() => store.isLoading);

/**
 * Computed property that returns the value of the 'filterQuery' property from the store.
 * @name filterQuery
 * @type {boolean}
 */
const filterQuery = computed(() => store.filterQuery);

/**
 * Computed property that checks if the store is empty.
 * @type {boolean}
 */
const isEmpty = computed(() => store.isEmpty);

// to check if the load more flag is true
// if true, a loading icon will show at the bottom of the scrollbar
const loadMoreFlag = computed(() => store.loadingMore);

// scroll to the top when a filter query is applied (apply/reset)
const { y } = useScroll(scroller, { behavior: 'smooth' });

/**
 * Watch for changes in the filter query to scroll to the top of the scroller.
 */
watch(filterQuery, (newValue) => {
  if (newValue && y.value > 0) {
    const el = document.getElementById('INTFCenter-scroller');
    el.scrollTo({ top: 0, behavior: 'smooth' });
    store.setFilterQuery(false);
  }
});

/**
 * Load more notifications when the user scrolls to the bottom of the scroller.
 */
useInfiniteScroll(
  scroller,
  () => {
    loadMoreNotifications();
  },
  {
    // check if can load more
    canLoadMore: () => {
      return store.canLoadMoreHistory === true || store.canLoadMoreFromArchive === true;
    },
    // distance from the bottom to trigger the load more
    distance: 50,
    // interval between each load more
    interval: 500,
  }
);
</script>

<template>
  <div class="INTFCenter-notif-center-base-center">
    <div class="INTFCenter-header-container">
      <CenterHeader />
    </div>
    <div class="INTFCenter-main-center-notification">
      <!-- if loadMoreFlag = true, a loading icon will show at the bottom of the scrollbar -->
      <div class="INTFCenter-base-center">
        <vu-scroller
          id="INTFCenter-scroller"
          ref="scroller"
          :infinite="loadMoreFlag"
          class="INTFCenter-center-body"
          :class="{ 'INTFCenter-empty-loading': loading || isEmpty, 'INTFCenter-loading-more': loadMoreFlag }"
        >
          <div v-if="loading" class="INTFCenter-spinner-container">
            <vu-spinner></vu-spinner>
          </div>
          <Transition name="center">
            <div v-if="!loading" class="INTFCenter-center-section">
              <NoNotification v-if="isEmpty" />
              <SectionList v-else />
            </div>
          </Transition>
        </vu-scroller>
      </div>
    </div>
  </div>
</template>
