<script setup>
import { useNow } from '@vueuse/core';
import { computed } from 'vue';
import useTranslations from '~/composables/useTranslations';
import { Setting } from '../../../../../models/setting';
import useSettingsStore from '../../../../../stores/settings';

const { $i18n } = useTranslations();
const now = useNow();
const props = defineProps({
  setting: {
    type: Setting,
    required: true,
  },
});

/**
 * Computes the remaining time.
 * @returns {number} The remaining time.
 */
const remainingTime = computed(() => {
  const { getSetting } = useSettingsStore();
  const setting = getSetting(props.setting.id);
  const currentTime = new Date(now.value).getTime();
  const unsubTime = new Date(setting.unsubscribe_date).getTime();
  //
  const elapsedMilliseconds = Math.abs(currentTime - unsubTime);
  const elapsedMinutes = Math.round(elapsedMilliseconds / (1000 * 60));
  // supposed time can't be more than an hour (need to be redefine if else in the future)
  return elapsedMinutes > 60 ? 60 : elapsedMinutes;
});

/**
 * Computed property that returns the status.
 * @returns {any} The computed status.
 */
const status = computed(() => {
  let status = $i18n('centerIconText');
  if (props.setting.notif_by_ui) status += ', ' + $i18n('alertIconText');
  if (props.setting.notif_by_email) status += ', ' + $i18n('mailIconText');
  if (props.setting.notif_by_browser) status += ', ' + $i18n('browserIconText');
  return status;
});

/**
 * Computed property that returns the status text.
 * @returns {string} The status text.
 */
const statusText = computed(() => {
  return props.setting.subscribe === 1
    ? props.setting.unsubscribe_date !== null
      ? `${$i18n('settingOffForAnHour')} (${remainingTime.value === 60 ? 1 : remainingTime.value} ${remainingTime.value === 60 ? $i18n('hourLeft') : $i18n('minLeft')})`
      : status.value
    : $i18n('settingOff');
});
</script>

<template>
  <div class="INTFCenter-preference-appStatus">
    {{ statusText }}
  </div>
</template>
