<script setup>
import { ref, watch, onBeforeMount } from 'vue';
import { useNotificationActions } from '~/composables/notification/useNotificationActions';
import { getWAFData } from '~/modules/imports';
import { NotificationTrackerUsage } from '../../../../../api/tracker';
// Will contain the number of tries to load the icon
const tries = ref(0);
const props = defineProps({
  id: String,
  group: {
    type: Boolean,
    default: false,
  },
  merge: {
    type: Boolean,
    default: false,
  },
  type: {
    type: String,
    default: 'login',
  },
  isLink: {
    type: Boolean,
    default: false,
  },
  icon: String,
});

const src = ref('');

const iconUrlBackup = ref('./../../resources/en/1/webapps/NotifAlert/assets/icons/notificon.png');

// go to the link if the icon is a link
const goToLink = () => {
  if (props.isLink) {
    const { openIconUrl } = useNotificationActions();
    const { centerActionTracker } = NotificationTrackerUsage();
    openIconUrl(props.icon);

    // analytics
    centerActionTracker().profileClick();
  }
};

// watch the icon prop and update the src
watch(
  () => props.icon,
  () => {
    src.value = props.icon;
    tries.value = 0;
  }
);

// load the default icon if the icon is not found after 3 tries
const loadDefault = (event) => {
  tries.value += 1;
  // if the icon is not found after 3 tries, load the default icon
  if (tries.value > 3) {
    event.target.src = iconUrlBackup.value;
  }
  // try to load the icon again
  else {
    src.value = `${props.icon}?retry=${tries.value}`;
  }
};

onBeforeMount(async () => {
  try {
    const WAFData = await getWAFData();
    if (WAFData) {
      src.value = WAFData.proxifyUrl(props.icon, { proxy: 'passport' }) || props.icon;
    }
  } catch (error) {
    console.error('Error loading icon', error);
  }
});
</script>

<template>
  <div
    :icon-src="icon"
    :class="{ 'cursor-pointer': isLink }"
    class="INTFCenter-notification-notificon"
    @click="goToLink"
  >
    <img
      :class="{ 'INTFCenter-IconRadius': type === 'login' || 'url', 'INTFCenter-mergeIcon': merge }"
      class="INTFCenter-bodyIcon"
      :src="src.length > 0 ? src : iconUrlBackup"
      @error="loadDefault"
    />
    <div v-if="group" class="INTFCenter-Mergecount">
      <span class="INTFCenter-bodyMergedImg fonticon fonticon-users"></span>
    </div>
  </div>
</template>
