<script setup>
import { computed, onMounted, ref, watch } from 'vue';
import { useNotificationActions } from '~/composables/notification/useNotificationActions';
import { getDateMergeFormat } from '~/functions/utils';
import { Notification } from '~/models/notification';
import { NotificationTrackerUsage } from '../../../../../api/tracker';
import NotificationActions from './NotificationActions.vue';
import NotificationDate from './NotificationDate.vue';

const props = defineProps({
  notification: Notification,
  isExpanded: {
    type: Boolean,
    default: false,
  },
  merge: {
    type: Boolean,
    default: false,
  }
});

// Date to display for sub notfications
const formattedDate = ref('');

/**
 * Represents the state of whether the selection is disabled or not.
 */
const selectDisabled = ref(false);

/**
 * Represents the NotificationMessage component.
 *
 * @prop {boolean} denyDisabled - Indicates whether the deny button is disabled.
 */
const denyDisabled = ref(false);

/**
 * Represents the deny reason values.
 */
const reasonValues = ref(null);

/**
 * Indicates whether an action need to be performed after fetching data.
 * @type {boolean}
 */
const actionAfterFetch = ref(false);

/**
 * Represents the value of the action to be performed after fetching data.
 * @type {Ref<number>}
 */
const iAfterFetch = ref(-1);

// Message link
const messageLink = ref('');

/**
 * Represents an action.
 */
const action = ref(null);

/**
 * Computed property that checks if merges are fetched.
 * @returns {boolean} True if merges are fetched, false otherwise.
 */
const isMergesFetched = computed(() => {
  return props.notification.isGroup === true && props.notification.mergesFetched === true;
});

// Watch for the merges to be fetched and open them
watch(isMergesFetched, (newVal) => {
  if (newVal) {
    if (actionAfterFetch.value) {
      if (iAfterFetch.value !== -1) {
        const { executeActionSelect } = useNotificationActions();
        executeActionSelect(props.notification, iAfterFetch.value);
      } else {
        const { executeDeny } = useNotificationActions();
        executeDeny(props.notification, reasonValues.value);
      }
      iAfterFetch.value = -1;
      actionAfterFetch.value = false;
    }
  }
});

// check if the first action is a select action.
const isFirstActionSelect = computed(() => {
  if (props?.notification?.MESSAGE?.actions && props.notification.MESSAGE.actions.length > 0) {
    return props.notification.MESSAGE.actions[0].type === 'select';
  }
  return false;
});
// check if the first action is a button action.
const isFirstActionButton = computed(() => {
  if (props?.notification?.MESSAGE?.actions && props.notification.MESSAGE.actions.length > 0) {
    return props.notification.MESSAGE.actions[0].type === 'button';
  }
  return false;
});

// Function to fetch and format the mergedate asynchronously.
const fetchMergeDate = async() => {
  let date = new Date(props.notification.CREATION_DATE.getTime());
  date.setHours(0, 0, 0, 0);

  // Waits for the date to be formatted and assigns it to formatteDate
  const formatted = await getDateMergeFormat(new Date(date.getTime()));
  formattedDate.value = formatted;
};

// to call fetchMergeDate after the component has mounted
onMounted(() => {
  fetchMergeDate();
});


// Open the link in a new tab
const navigateToLink = () => {
  if (isMessageLink()) {
    const { NavigateToLink } = useNotificationActions();
    const { centerActionTracker } = NotificationTrackerUsage();
    NavigateToLink(props.notification, action);

    // analytics
    centerActionTracker().notifClick();
  }
};

/**
 * handleContainerClick: to handle the click of two elements: the click on the message to open it
 * And the click on 'View More' and 'View Less' to expand or collapse a long message 
 */
const handleContainerClick = (event) => {  
  if (event.target.classList.contains('toggle-link')) {
    event.stopPropagation();
    props.isExpanded = !props.isExpanded;
  } else {
    navigateToLink();
  }
};

/**
 * formattedMessage manages the display of the message depending on its length.
 * handles the 'View More' and 'View Less' if the message's length exceeds the maxLength
 */
const formattedMessage = computed(() => {
  const message = props.notification.MESSAGE.nls.msg;
  const maxLength = 256;
  let result = props.isExpanded ? message : (message.length > maxLength ? message.slice(0, maxLength) + '...' : message);

  if (message.length > maxLength) {
    result += "<a href='#' class='toggle-link'>" + (props.isExpanded ? ' View Less' : ' View More') + '</a>';
  }
  return result;
});

// Check if the message can be opened as a link
const isMessageLink = () => {
  const { noticationMessageIsLink } = useNotificationActions();
  // if the message is a link, return a boolean & set the option (action)
  const { option, isLink } = noticationMessageIsLink(props.notification.MESSAGE.actions);
  if (isLink) {
    messageLink.value = option.options.event.options.url;
    // get the action to be used in the link
    action.value = option;
    return true;
  }
  return false;
};

/**
 * Accepts an action for a notification message.
 *
 * @param {Object} options - The options for the action.
 * @param {number} i - The index of the action.
 */
const _acceptAction = ({ options, i }) => {
  const { acceptAction } = useNotificationActions();
  acceptAction(props.notification, { options, i }, selectDisabled, actionAfterFetch, iAfterFetch);
};

/**
 * Denies the action for the notification.
 *
 * @param {Object} options - The options for denying the action.
 */
const _denyAction = (options) => {
  const { denyAction } = useNotificationActions();
  denyAction(props.notification, options, reasonValues, denyDisabled, actionAfterFetch);
};
</script>

<template>
  <div class="INTFCenter-notification-message-container">
    <div class="INTFCenter-message-merge-date">
      <div
        class="INTFCenter-color-brown INTFCenter-notification-message"
        :class="{'cursor-pointer': isMessageLink() }"
        @click="handleContainerClick"
        v-html="formattedMessage"
      >
      </div>

      <!--  -->
      <NotificationDate
        v-if="merge"
        :date="formattedDate"
        :is-merge="true"
        :is-read="notification.isRead"
        :time="notification.getHourMinutes()"
      />
    </div>
    <!--  -->
    <div v-if="isFirstActionSelect || isFirstActionButton" :class="{ 'self-end': !notification.isActioned }">
      <!-- Notifications actions request -->
      <NotificationActions
        :select-disabled="selectDisabled"
        :deny-disabled="denyDisabled"
        :is-actioned="notification.isActioned"
        :actioned-label="notification.ACTION"
        :is-read="notification.isRead"
        :actions="notification.MESSAGE.actions"
        :is-select="isFirstActionSelect"
        :is-button="isFirstActionButton"
        @accept="_acceptAction"
        @deny="_denyAction"
      />
    </div>
  </div>
</template>
