<script setup>
// import { getMerge } from '~/api/senders';
import { computed, ref, watch } from 'vue';
import { getMerge } from '~/api/senders';
import { useNotificationManagement } from '~/composables/notification/useNotificationManagement';
import useTranslations from '~/composables/useTranslations';
import useNotificationsStore from '~/stores/notifications';
import useFilterStore from '~/stores/filter';
import { NotificationTrackerUsage } from '../../../../api/tracker';
import NotificationBody from './NotificationBody/NotificationBody.vue';
import NotificationDeleted from './NotificationDeleted.vue';

const { $i18n } = useTranslations();
const { cancelDeletion } = useNotificationManagement();
const { centerActionTracker } = NotificationTrackerUsage();
const theCenterActionTracker = centerActionTracker();
const store = useNotificationsStore();
const { getFilter } = useFilterStore();

const components = {
  NotificationBody,
  NotificationDeleted,
};
const props = defineProps({
  mainId: String,
  groupId: String,
  isRead: {
    type: Boolean,
    default: false,
  },
});

/**
 * Represents the state of whether the notification merges list is opened or not.
 * @type {Ref<boolean>}
 */
const opened = ref(false);

/**
 * The index of the merge that are being read.
 */
const mergeIndex = ref(0);

/**
 * Indicates whether the merge list has been requested to be opened.
 * @type {Ref<boolean>}
 */
const openRequested = ref(false);

/**
 * Computes the main notification.
 * @returns {ComputedRef} The computed main notification.
 */
const getMainNotification = computed(() => {
  return store.getNotificationById(props.mainId);
});

/**
 * Computed property that determines if merges can be opened.
 * @returns {boolean} True if merges can be opened, false otherwise.
 */
const canOpenMerges = computed(() => {
  return getMainNotification.value.mergesFetched === true;
});

/**
 * Computed property that returns the group.
 * @returns {Array} The computed group.
 */
const allMerges = computed(() => {
  const groupSection = store.groups.get(props.groupId);
  return groupSection;
});

/**
 * Computed property that returns the read group.
 * @returns {Array} The computed read group.
 */
const groupMerges = computed(() => {
  return allMerges.value.slice(0, mergeIndex.value + 10);
});

// Watch for the merges to be fetched and open them if requested
watch(canOpenMerges, (newVal) => {
  if (newVal && openRequested.value) {
    opened.value = true;
    openRequested.value = false;
  }
});

// watch opened then reset the mergeIndex
watch(opened, () => {
  if (!opened.value) {
    mergeIndex.value = 0;
  }
});

// Get the merged notifications
const getMerges = () => {
  // if the merges are not fetched yet, fetch them
  if (!getMainNotification.value.mergesFetched) {
    // todo: handle type !== 'all'
    getMerge({
      parentNotif: {
        id: getMainNotification.value.ID,
        groupID: getMainNotification.value.GROUPID,
        clusterId: getMainNotification.value.CLUSTERID,
        archive: getMainNotification.value.ARCHIVE,
        ignoreSearchesForChildren: !!getMainNotification.value.ignoreSearchesForChildren,
      },
      filterData: getFilter(),
    });
    // to open the merges when they are fetched
    openRequested.value = true;
    // analytics
    theCenterActionTracker.viewAllMergeNotif({ nbMerge: getMainNotification.value.COUNT });
  }
  // else open the merges or close them
  else {
    opened.value = !opened.value;

    // analytics
    if (opened.value) {
      theCenterActionTracker.viewAllMergeNotif({ nbMerge: getMainNotification.value.COUNT });
    } else {
      theCenterActionTracker.hideAllMergeNotif({ nbMerge: getMainNotification.value.COUNT });
    }
  }
};
const seeMoreMerge = () => {
  mergeIndex.value += 10;
};
</script>

<template>
  <div v-if="getMainNotification" class="INTFCenter-notification-group">
    <!-- View all/Hide all -->
    <vu-btn :class="{ 'INTFCenter-is-read': isRead }" small class="INTFCenter-view-btn" @click="getMerges">
      <div class="INTFCenter-view-merge-div">
        <span
          class="fonticon INTFCenter-color-brown fonticon-expand-right"
          :class="{
            'INTFCenter-merges-opened': opened,
            'INTFCenter-merges-closed': !opened,
          }"
        ></span>
        <div class="INTFCenter-show-hide-merges">
          <span v-if="!opened" class="INTFCenter-color-3ds INTFCenter-view-group">{{ $i18n('viewAll') }}</span>
          <span v-else class="INTFCenter-color-3ds INTFCenter-view-group">{{ $i18n('hideAll') }}</span>
        </div>
        <span class="INTFCenter-open-spinner" v-if="openRequested"><vu-spinner></vu-spinner></span>
      </div>
    </vu-btn>
    <Transition name="merge-list">
      <div v-if="opened" class="INTFCenter-group-div">
        <!-- Introduce the cancellation of merge deleted here too -->
        <TransitionGroup name="list">
          <div v-for="merge in groupMerges" :key="merge.id">
            <component
              :is="components[store.getMergeById(merge.id).deleted ? 'NotificationDeleted' : 'NotificationBody']"
              :id="merge.id"
              :key="`${merge.id} - merge`"
              :merge="true"
              class="INTFCenter-group-body"
              :notification="store.getMergeById(merge.id)"
              @cancel-deletion="(id) => cancelDeletion(id, true)"
            />
          </div>
        </TransitionGroup>
        <!-- See more -->
        <div v-if="groupMerges.length < allMerges.length" :class="{ 'INTFCenter-is-read': isRead }">
          <span class="INTFCenter-color-3ds INTFCenter-view-group cursor-pointer" @click="seeMoreMerge">{{ $i18n('viewMore') }}</span>
        </div>
      </div>
    </Transition>
  </div>
</template>
