<script setup>
import AppTitle from './App/AppTitle.vue';
import AppStatus from './App/AppStatus.vue';
import AppIcon from './App/AppIcon.vue';
import { Setting } from '../../../../models/setting';
import useSettingsStore from '../../../../stores/settings';
import { NotificationTrackerUsage } from '../../../../api/tracker';
const store = useSettingsStore();
const emit = defineEmits(['openAppSetting']);
const props = defineProps({
  setting: {
    type: Setting,
    required: true,
  },
});

/**
 * Opens the application settings.
 */
const openAppSetting = () => {
  store.setCurrentSetting(props.setting.id);
  //
  store.setAppSettingOpened(true);
  // analytics
  const { notificationSettingViewTracker } = NotificationTrackerUsage();
  notificationSettingViewTracker().serviceSettingsView({
    persDim: {
      pd2: props.setting.servicename,
      pd3: props.setting.service,
    },
  });
};
</script>
<template>
  <div
    v-if="setting"
    class="INTFCenter-preference-appname"
    :class="{ 'INTFCenter-preference-appname-off': !setting.enable }"
    @click="openAppSetting"
  >
    <AppIcon :setting="setting"></AppIcon>
    <div class="INTFCenter-pref-appname-right">
      <div>
        <AppTitle :appTitle="setting.servicename"></AppTitle>
        <AppStatus :setting="setting"></AppStatus>
      </div>
      <div class="INTFCenter-btn-transparent-default INTFCenter-btn-hover INTFCenter-app-setting-btn">
        <span class="fonticon fonticon-chevron-right INTFCenter-back-icon"></span>
      </div>
    </div>
  </div>
</template>
