<script setup>
import { computed, ref } from 'vue';
import useTranslations from '~/composables/useTranslations';
const { $i18n } = useTranslations();
const emit = defineEmits(['deny', 'accept']);

const props = defineProps({
  isActioned: {
    type: Boolean,
    default: false,
  },
  actionedLabel: {
    type: String,
    default: '',
  },
  actions: Object,
  isRead: {
    type: Boolean,
    default: false,
  },
  denyDisabled: {
    type: Boolean,
    default: false,
  },
  selectDisabled: {
    type: Boolean,
    default: false,
  },
  isSelect: {
    type: Boolean,
    default: false,
  },
  isButton: {
    type: Boolean,
    default: false,
  },
});
const target = ref(null);

/**
 * Computed property that returns the label for a request.
 *
 * @returns {string} The label for the request.
 */
const requestLabel = computed(() => {
  if (props.actionedLabel && typeof props.actionedLabel === 'string') {
    if (props.actionedLabel.indexOf('NLS') === -1) {
      return props.actionedLabel;
    }
    const dotIndex = props.actionedLabel.indexOf('.');
    return $i18n(props.actionedLabel.substring(dotIndex + 1));
  }
  return $i18n(actionLabel);
});

// Get the accept button label
const acceptLabel = computed(() => {
  const findOptions = props.isSelect ? props.actions.find((action) => action.type === 'select')?.options?.event?.options[0] : props.actions[0]?.options;
  return findOptions?.label || $i18n('accept');
});

// Get the deny button label
const denyLabel = computed(() => {
  const findOptions = props.isSelect ? props.actions.find((action) => action.type === 'button')?.options : props.actions[1]?.options;
  return findOptions?.label || $i18n('deny');
});
// Build the actions options list dropdown
const options = computed(() => {
  const items = [];
  const findAction = props.actions.find((action) => action.type === 'select');
  if (findAction) {
    const length = findAction.options.event.options.length;
    for (let i = 1; i < length; i++) {
      items.push({
        text: findAction.options.event.options[i].label,
        fonticon: '',
        label: findAction.options.event.options[i].label,
        handler: (item) => {
          const action = {
            type: findAction.options.event.type,
            subAction: true,
            action: findAction.options.event.options[i],
          };
          acceptAction({ options: action, i });
        },
      });
    }
  }
  return items;
});

/**
 * Accepts the notification action.
 *
 * @param {Object} options - The options for the action.
 * @param {number} i - The index of the action.
 */
const acceptAction = ({ options, i }) => {
  emit('accept', { options, i });
};

/**
 * Deny action function.
 */
const denyAction = () => {
  const findAction = props.isSelect ? props.actions.find((action) => action.type === 'button') : props.actions[1];
  if (findAction) {
    const action = {
      type: findAction.options.event.type,
      action: findAction,
    };
    emit('deny', action);
  }
};
/**
 * Function to handle the drop button action.
 */
const btnDrop = () => {
  acceptAction({
    options: {
      type: props.actions[0].options.event.type,
      subAction: true,
      action: props.actions[0].options.event.options[0],
    },
    i: 0,
  });
};
/**
 * Function to handle the accept button action.
 */
 const btnAccept = () => {
  acceptAction({
    options: {
      type: props.actions[0].options.event.type,
      subAction: false,
      action: props.actions[0].options.event.options,
    },
    i: 0,
  });
};
</script>

<template>
  <!-- Notifications actions request -->
  <div class="INTFCenter-actions-container">
    <div v-if="isActioned">
      <strong>{{ requestLabel }}</strong>
    </div>
    <div v-else class="INTFCenter-notification-request-action" :class="{ disabled: selectDisabled || denyDisabled }">
      <div ref="target" class="INTFCenter-action-request">
        <vu-btn-dropdown
          v-if="isSelect"
          @click="btnDrop"
          :position="'bottom-right'"
          :responsive="true"
          :attach="target"
          color="primary"
          class="INTFCenter-request-action-btn-vuekit"
          :options="options"
        >
          {{ acceptLabel }}
        </vu-btn-dropdown>
        <vu-btn 
          v-else-if="isButton"
          small
          @click="btnAccept"
          :position="'bottom-right'"
          :responsive="true"
          :attach="target"
          color="primary"
          class="INTFCenter-request-action-btn-vuekit"
        >
          {{ acceptLabel }}
        </vu-btn>
      </div>
      <vu-btn
        small
        :class="{ 'INTFCenter-is-read': isRead }"
        class="INTFCenter-deny-btn"
        @click="denyAction"
      >
        {{ denyLabel }}
      </vu-btn>
    </div>
  </div>
</template>
