<script setup>
import { onMounted, ref, watch } from 'vue';
import useTranslations from '~/composables/useTranslations';
import { updateSettings } from '../../../../api/senders';
import { usePreferencesManagement } from '../../../../composables/preferences/usePreferencesManagement';
import { Setting } from '../../../../models/setting';
import AppTitle from './App/AppTitle.vue';
import GlobalAppSettingsList from './App/GlobalAppSettingsList.vue';

const { $i18n } = useTranslations();
const enabled = ref(null);
const showOptions = ref(false);
const props = defineProps({
  setting: {
    type: Setting,
    required: true,
  },
});

const options = ref([
  {
    value: 'on',
  },
]);

// useful when subscribe status changes (will update the enabled value)
watch(
  () => props.setting.subscribe,
  (value) => {
    if (value) enabled.value = 'on';
    else enabled.value = null;
  },
  { immediate: true }
);

/**
 * Toggles the options for the home list settings.
 */
const toggleOptions = () => {
  // show the plus and minus icon if the setting is enabled
  if (enabled.value) showOptions.value = !showOptions.value;
};
//
/**
 * Updates the settings with the given value.
 *
 * @param {any} value - The new value for the settings.
 */
const updateTheSettings = (value) => {
  if (!props.setting.readOnly) {
    updateSettings({
      subscribe: value ? 1 : 0,
      id: props.setting.id,
      notif_by_browser: props.setting.notif_by_browser,
      notif_by_email: props.setting.notif_by_email,
      notif_by_ui: props.setting.notif_by_ui,
    });
  } else {
    // message to the user
    const { messageNotice } = usePreferencesManagement();
    messageNotice($i18n('forcedPreference'), 3000);
  }
};

onMounted(() => {
  enabled.value = props.setting.subscribe === 1 ? 'on' : null;
});
</script>
<template>
  <!-- show individual preferences for a setting -->
  <div v-if="setting" class="INTFCenter-appList-container INTFCenter-preferences">
    <div
      class="INTFCenter-appList-top"
      :class="{
        'INTFCenter-preference-appname-off': setting.readOnly || !setting.enable,
      }"
    >
      <div class="INTFCenter-appList-top-setting">
        <div class="INTFCenter-appSetting-mask">
          <!-- todo: add ':class="{ 'cursor-pointer': enabled !== null }"' attribute below when individual setting options are enable -->
          <AppTitle :appTitle="setting.servicename" class="INTFCenter-appList-title" @click="toggleOptions"></AppTitle>
          <!-- <vu-btn v-if="enabled" small class="INTFCenter-btn-transparent-default" @click="toggleOptions">
            <span
              :class="{
                'INTFCenter-setting-opened': showOptions,
                'INTFCenter-setting-closed': !showOptions,
              }"
              class="fonticon fonticon-chevron-down INTFCenter-setting-chevron-down"
            ></span>
          </vu-btn> -->
        </div>
        <div class="INTFCenter-appList-top-option">
          <vu-checkbox
            type="switch"
            :disabled="setting.readOnly"
            :options="options"
            v-model="enabled"
            @update:modelValue="updateTheSettings"
          >
          </vu-checkbox>
        </div>
      </div>
      <!-- todo: uncoment this to show/hide options on top of the global setting -->
      <!-- <Transition name="app-list">
        <AppOptions
          v-if="showOptions"
          :setting="setting"
          :enabled="enabled !== null"
          :title="$i18n('globalSetting')"
        ></AppOptions>
      </Transition> -->
    </div>
    <!--  -->
    <GlobalAppSettingsList :setting="setting" :enabled="enabled !== null"></GlobalAppSettingsList>
  </div>
</template>
