<script setup>
// TODO rendre le dropdown customisable et le mettre dans un
// autre composant avec des props
import { computed, ref } from 'vue';
import NotificationBody from '~/components/BaseCenter/CenterBody/Notification/NotificationBody/NotificationBody.vue';
import NotificationDate from '~/components/BaseCenter/CenterBody/Notification/NotificationBody/NotificationDate.vue';
import NotificationStatus from '~/components/BaseCenter/CenterBody/Notification/NotificationBody/NotificationStatus.vue';
import NotificationMergesList from '~/components/BaseCenter/CenterBody/Notification/NotificationMergesList.vue';
import SelectWrapper from '~/components/BaseCenter/CenterHeader/Toolbar/SelectWrapper.vue';
import { Notification } from '~/models/notification';
import useNotificationsStore from '~/stores/notifications';
// not using destructuring to avoid non reactive properties
const store = useNotificationsStore();

const select = ref(false);

const props = defineProps({
  notification: Notification,
});

// check if the selection mode is on
const selectionMode = computed(() => {
  return store.selectionMode === true;
});

/**
 * Computed property that returns true if the notification is selected.
 *
 * @returns {any} The selected value.
 */
const selected = computed(() => {
  return store.selected.includes(props.notification.ID);
});

/**
 * Applies the given selection.
 *
 * @param {any} value - The value to apply.
 */
const applySelect = (value) => {
  select.value = value;
};
</script>

<template>
  <div
    v-if="notification"
    class="INTFCenter-notification-item-container"
    :class="{ 'INTFCenter-notif-is-selected': selected, 'INTFCenter-selection-mode-on': selectionMode }"
  >
    <TransitionGroup name="wrapper">
      <SelectWrapper v-if="selectionMode" :id="notification.ID" @select="applySelect" />
    </TransitionGroup>

    <div class="INTFCenter-notification-item">
      <!-- Notification date -->
      <NotificationDate
        :is-read="notification.isRead"
        :date="notification.getHourMinutes()"
      />
      <div :class="['INTFCenter-notif-body ' + notification.APPID]">
        <div class="INTFCenter-notif-body-ui">
          <!-- Read/Unread/Important icon -->
          <NotificationStatus :is-read="notification.isRead" :is-starred="notification.isStarred" :notification="notification"/>
          <!-- NotifAlert -->
          <NotificationBody class="INTFCenter-notif-body-component" :notification="notification" />
        </div>
        <!-- Merges area -->
        <NotificationMergesList
          v-if="notification.isGroup"
          :main-id="notification.ID"
          :is-read="notification.isRead"
          :group-id="notification.GROUPID"
        />
      </div>
    </div>
  </div>
</template>
