<script setup>
import { computed, onMounted, ref, watch } from 'vue';
// todo: uncomment below to show the options for individual settings
// import AppOptions from './Options/AppOptions.vue';
import useTranslations from '~/composables/useTranslations';
import { updateSettings } from '../../../../../api/senders';
import { usePreferencesManagement } from '../../../../../composables/preferences/usePreferencesManagement';
import { Setting } from '../../../../../models/setting';
import useSettingsStore from '../../../../../stores/settings';

const { $i18n } = useTranslations();
const store = useSettingsStore();
const showOptions = ref(false);
const props = defineProps({
  setting: {
    type: Setting,
    required: true,
  },
});
// todo: can use composables here
const enabled = ref(null);

/**
 * Computes the value of the globalSettingEnabled property.
 *
 * @returns {boolean} return true if the global setting is enabled, false otherwise.
 */
const globalSettingEnabled = computed(() => {
  const group = store.getSettingGroup(props.setting);
  return group && group.enable;
});

/**
 * options to manage the setting.
 */
const options = ref([
  {
    value: 'on',
  },
]);

/**
 * Toggles the options for the app settings.
 */
const toggleOptions = () => {
  // show the plus and minus icon if the setting is enabled
  if (enabled.value) showOptions.value = !showOptions.value;
};

// useful when subscribe status changes (will update the enabled value)
watch(
  () => props.setting.subscribe,
  (value) => {
    if (value) enabled.value = 'on';
    else {
      enabled.value = null;
      if (showOptions.value) {
        showOptions.value = false;
      }
    }
  },
  { immediate: true }
);

/**
 * Updates the settings with the given value.
 *
 * @param {any} value - The new value for the settings.
 */
const updateTheSettings = (value) => {
  if (props.setting.readOnly) {
    const { messageNotice } = usePreferencesManagement();
    messageNotice($i18n('forcedPreference'), 3000);
    return;
  }
  updateSettings({
    subscribe: value ? 1 : 0,
    id: props.setting.id,
    notif_by_browser: props.setting.notif_by_browser,
    notif_by_email: props.setting.notif_by_email,
    notif_by_ui: props.setting.notif_by_ui,
  });
};

onMounted(() => {
  enabled.value = props.setting.enable ? 'on' : null;
});
</script>
<template>
  <div
    class="INTFCenter-appSetting-container"
    :class="{ 'INTFCenter-preference-appname-off': setting.readOnly || !setting.enable || !globalSettingEnabled }"
  >
    <div class="INTFCenter-appSetting-top">
      <div class="INTFCenter-appSetting-mask">
        <!-- todo: add ':class="{ 'cursor-pointer': enabled }"' attribute below when individual setting options are enable -->
        <div class="INTFCenter-appSetting-appTitle" @click="toggleOptions">
          {{ setting.name }}
        </div>
        <!-- todo: uncomment below to show the options for individual settings -->
        <!-- <vu-btn v-if="enabled" small class="INTFCenter-btn-transparent-default" @click="toggleOptions">
          <span
            :class="{
              'INTFCenter-setting-opened': showOptions,
              'INTFCenter-setting-closed': !showOptions,
            }"
            class="fonticon fonticon-chevron-down INTFCenter-setting-chevron-down"
          ></span>
        </vu-btn> -->
      </div>
      <div class="INTFCenter-appSetting-option">
        <vu-checkbox
          type="switch"
          :disabled="setting.readOnly"
          :options="options"
          v-model="enabled"
          @update:modelValue="updateTheSettings"
        >
        </vu-checkbox>
      </div>
    </div>
    <!-- todo: uncomment below to show the options for individual settings -->
    <!-- <Transition name="app-list">
      <AppOptions v-if="showOptions" :setting="setting" :enabled="showOptions"></AppOptions>
    </Transition> -->
  </div>
</template>
