<script setup>
import { useNow } from '@vueuse/core';
import { computed, onMounted, ref, watch } from 'vue';
import useTranslations from '~/composables/useTranslations';
import { updateUnsubscribeDate } from '../../../../../../api/senders';
import { NotificationTrackerUsage } from '../../../../../../api/tracker';
import { usePreferencesManagement } from '../../../../../../composables/preferences/usePreferencesManagement';
import { getDateAfter1Hour } from '../../../../../../functions/utils';
import { Setting } from '../../../../../../models/setting';
import useSettingsStore from '../../../../../../stores/settings';

const { $i18n } = useTranslations();
const store = useSettingsStore();
const now = useNow();

const unsubedOneHour = ref(false);

const props = defineProps({
  setting: {
    type: Setting,
    required: true,
  },
  title: {
    type: String,
    default: '',
  },
  unsubHour: {
    type: Boolean,
    default: false,
  },
  enabled: {
    type: Boolean,
    required: true,
  },
});

/**
 * Computes the remaining time.
 * @returns {number} The remaining time.
 */
const remainingTime = computed(() => {
  const setting = store.getSetting(props.setting.id);
  const currentTime = new Date(now.value).getTime();
  const unsubTime = new Date(setting.unsubscribe_date).getTime();

  const elapsedMilliseconds = Math.abs(currentTime - unsubTime);
  const elapsedMinutes = Math.round(elapsedMilliseconds / (1000 * 60));
  // supposed time can't be more than an hour (need to be redefine if else in the future)
  return elapsedMinutes > 60 ? 60 : elapsedMinutes;
});

/**
 * Computed property for unsubedOneHourText.
 * @returns {any} The computed value of unsubedOneHourText.
 */
const unsubedOneHourText = computed(() => {
  return `${$i18n('unsubscribeForHour')} ${unsubedOneHour.value ? `(` + (remainingTime.value === 60 ? 1 : remainingTime.value) + ' ' + (remainingTime.value === 60 ? $i18n('hourLeft') : $i18n('minLeft')) + ')' : ''}`;
});

const globalSetting = computed(() => {
  return store.getSettingGroup(props.setting);
});

/**
 * Computes the value of `globalAlertOnSettingOff`.
 * @returns {ComputedRef} The computed value of `globalAlertOnSettingOff`.
 */
const globalAlertOnSettingOff = computed(() => {
  return globalSetting.value && !props.setting.isGroup && globalSetting.value.notif_by_ui === 1;
});

/**
 * Computed property that returns the global mail on setting when it is turned off.
 * @returns {any} The computed value of the global mail on setting when it is turned off.
 */
const globalMailOnSettingOff = computed(() => {
  return globalSetting.value && !props.setting.isGroup && globalSetting.value.notif_by_email === 1;
});

/**
 * Computed property that returns the global browser on setting off.
 * @returns {any} The computed value of the global browser on setting off.
 */
const globalBrowserOnSettingOff = computed(() => {
  return globalSetting.value && !props.setting.isGroup && globalSetting.value.notif_by_browser === 1;
});

/**
 * Unsubscribes the user from notifications for an hour.
 * @param {any} value - The value to pass to the function.
 */
const unsubForAnHour = (value) => {
  const { notificationSettingViewTracker } = NotificationTrackerUsage();
  const settingsViewTracker = notificationSettingViewTracker();

  if (props.setting.readOnly) {
    const { messageNotice } = usePreferencesManagement();
    messageNotice($i18n('forcedPreference'), 3000);
    return;
  }
  // send the event here
  const date = getDateAfter1Hour(new Date(Date.now()));

  updateUnsubscribeDate({
    id: props.setting.id,
    unsubscribe_date: value ? date : null,
    // frontEndUpdate: true, // put this to false to let server update the date
  });

  if (!value) {
    // unsubscribe_date = null
    // remove the setting from the watchList
    store.removeUnsubscribe_dateSetting(props.setting.id);
    // analytics
    settingsViewTracker.resetUnsubscribeNotificationDate({
      persDim: {
        pd1: props.setting.name,
        pd2: props.setting.servicename,
        pd3: props.setting.service,
        // pd4: 'for an hour',
        // pd5: 'setting service',
        // no need to send pd4 and pd5 because we know where it is coming from (setting service)
      },
    });
  } else {
    // analytics
    settingsViewTracker.unsubscribeNotification({
      persDim: {
        pd1: props.setting.name,
        pd2: props.setting.servicename,
        pd3: props.setting.service,
        pd4: 'for an hour',
        pd5: 'setting service',
      },
    });
  }
};
//
watch(
  () => props.setting.unsubscribe_date,
  (value) => {
    if (value !== null) {
      unsubedOneHour.value = true;
    } else {
      unsubedOneHour.value = false;
    }
  }
);

/**
 * Updates the setting based on the given type.
 * @param {string} type - The type of setting to update.
 */
const updateSetting = (type) => {
  const readOnly = {
    notif_by_ui: () => props.setting.notif_by_uiReadOnly,
    notif_by_email: () => props.setting.notif_by_emailReadonly,
    notif_by_browser: () => props.setting.notif_by_browserReadonly,
  };
  
  if (props.setting.readOnly || (Object.hasOwn(readOnly, type) && readOnly[type]())) {
    const { messageNotice } = usePreferencesManagement();
    messageNotice($i18n('forcedPreference'), 3000);
    return;
  } else {
    if (type === 'center') {
      return;
    } else {
      const { settingUpdate } = usePreferencesManagement();
      const setting = {
        id: props.setting.id,
        notif_by_browser:
          type === 'notif_by_browser' ? (props.setting.notif_by_browser === 1 ? 0 : 1) : props.setting.notif_by_browser,
        notif_by_email:
          type === 'notif_by_email' ? (props.setting.notif_by_email === 1 ? 0 : 1) : props.setting.notif_by_email,
        notif_by_ui: type === 'notif_by_ui' ? (props.setting.notif_by_ui === 1 ? 0 : 1) : props.setting.notif_by_ui,
      };
      //
      settingUpdate(setting, type);
    }
  }
};

onMounted(() => {
  unsubedOneHour.value = props.setting.unsubscribe_date !== null;
});
</script>

<template>
  <div class="INTFCenter-option" :class="{ 'INTFCenter-preference-appname-off': setting.readOnly || !setting.enable }">
    <div class="INTFCenter-option-container">
      <!-- todo: this feature allow the user to resube back before the end of the one hour -->
      <!-- unsub one hour checkbox -->
      <!-- <div class="INTFCenter-unsub">
        <vu-single-checkbox
          v-model="unsubedOneHour"
          :label="unsubedOneHourText"
          class="INTFCenter-appSetting-hourUnsub"
          @update:modelValue="unsubForAnHour"
          :disabled="setting.readOnly"
        >
        </vu-single-checkbox>
      </div> -->
      <!-- list of option -->
      <div class="INTFCenter-option-list">
        <!-- Notifcenter -->
        <div
          @click="updateSetting('center')"
          class="INTFCenter-desktop INTFCenter-option-center INTFCenter-desktop-disabled"
          :title="$i18n('centerNotificationTooltipEnabled')"
        >
          <span class="icon-desktop-center"></span>
          <span class="INTFCenter-desktop-center-intfnotif-on">{{ $i18n('centerIconText') }}</span>
        </div>
        <!-- alert -->
        <div
          :class="{
            'INTFCenter-option-list-disabled': setting.notif_by_uiReadOnly || unsubedOneHour,
            'INTFCenter-preference-appname-off': globalAlertOnSettingOff,
          }"
          @click="updateSetting('notif_by_ui')"
          class="INTFCenter-desktop INTFCenter-option-alert"
          :title="
            (setting.notif_by_ui === 1
              ? $i18n('alertNotificationTooltipEnabled')
              : $i18n('alertNotificationTooltipDisabled') ) + (globalAlertOnSettingOff ? ', ' + $i18n('overrideByGlobal') : '')
          "
        >
          <span
            class="icon-desktop-popover"
            :class="{
              'INTFCenter-desktop-popover-intfnotif-on': setting.notif_by_ui === 1,
              'INTFCenter-desktop-popover-intfnotif-off': setting.notif_by_ui === 0,
            }"
          ></span>
          <span
            :class="{
              'INTFCenter-desktop-popover-intfnotif-on': setting.notif_by_ui === 1,
              'INTFCenter-desktop-popover-intfnotif-off': setting.notif_by_ui === 0,
            }"
            >{{ $i18n('alertIconText') }}</span
          >
        </div>
        <!-- mail -->
        <div
          :class="{
            'INTFCenter-option-list-disabled': setting.notif_by_emailReadonly || unsubedOneHour,
            'INTFCenter-preference-appname-off': globalMailOnSettingOff,
          }"
          @click="updateSetting('notif_by_email')"
          class="INTFCenter-desktop INTFCenter-option-email"
          :title="
            (setting.notif_by_email === 1
              ? $i18n('mailNotificationTooltipEnabled')
              : $i18n('mailNotificationTooltipDisabled')) + (globalMailOnSettingOff ? ', ' + $i18n('overrideByGlobal') : '')
          "
        >
          <span
            class="icon-desktop-email"
            :class="{
              'INTFCenter-desktop-email-intfnotif-on': setting.notif_by_email === 1,
              'INTFCenter-desktop-email-intfnotif-off': setting.notif_by_email === 0,
            }"
          ></span>
          <span
            :class="{
              'INTFCenter-desktop-email-intfnotif-on': setting.notif_by_email === 1,
              'INTFCenter-desktop-email-intfnotif-off': setting.notif_by_email === 0,
            }"
            >{{ $i18n('mailIconText') }}</span
          >
        </div>
        <!-- browser -->
        <div
          :class="{
            'INTFCenter-option-list-disabled': setting.notif_by_browserReadonly || unsubedOneHour,
            'INTFCenter-preference-appname-off': globalBrowserOnSettingOff,
          }"
          @click="updateSetting('notif_by_browser')"
          class="INTFCenter-desktop INTFCenter-option-browser"
          :title="
            (setting.notif_by_browser === 1
              ? $i18n('browserNotificationTooltipEnabled')
              : $i18n('browserNotificationTooltipDisabled')) +
            (globalBrowserOnSettingOff ? ', ' + $i18n('overrideByGlobal') : '')
          "
        >
          <span
            class="icon-desktop-empty"
            :class="{
              'INTFCenter-desktop-empty-intfnotif-on': setting.notif_by_browser === 1,
              'INTFCenter-desktop-empty-intfnotif-off': setting.notif_by_browser === 0,
            }"
          ></span>
          <span
            :class="{
              'INTFCenter-desktop-empty-intfnotif-on': setting.notif_by_browser === 1,
              'INTFCenter-desktop-empty-intfnotif-off': setting.notif_by_browser === 0,
            }"
            >{{ $i18n('browserIconText') }}</span
          >
        </div>
      </div>
    </div>
  </div>
</template>
