<script setup>
import useTranslations from '~/composables/useTranslations';

const { $i18n } = useTranslations();
const emit = defineEmits(['close', 'apply', 'reset']);

const closeFilter = () => {
  emit('close');
};
const resetFilter = () => {
  emit('reset');
};
const applyFilter = () => {
  emit('apply');
};
</script>

<template>
  <div class="INTFCenter-filter-footer">
    <div class="INTFCenter-filter-bottom-container">
      <div class="INTFCenter-bottom-filter">
        <vu-btn color="primary" class="INTFCenter-filter-btn-filter" @click="applyFilter">{{ $i18n('filter') }}</vu-btn>
        <vu-btn class="INTFCenter-filter-btn-reset" @click="resetFilter">{{ $i18n('reset') }}</vu-btn>
        <vu-btn class="INTFCenter-filter-btn-close" @click="closeFilter">{{ $i18n('close') }}</vu-btn>
      </div>
    </div>
  </div>
</template>
