<script setup>
import NotificationItem from '~/components/BaseCenter/CenterBody/Notification/NotificationItem.vue';
import NotificationDeleted from '~/components/BaseCenter/CenterBody/Notification/NotificationDeleted.vue';
import { Section } from '~/models/section';
import useNotificationsStore from '~/stores/notifications';
import { useNotificationManagement } from '../../../composables/notification/useNotificationManagement';
const { getNotificationById } = useNotificationsStore();
const { cancelDeletion } = useNotificationManagement();
const components = {
  NotificationItem,
  NotificationDeleted,
};
defineProps({
  section: Section,
});
</script>

<template>
  <div v-if="section" class="INTFCenter-notif-center-timeline-section">
    <div class="INTFCenter-timeline-section-title INTFCenter-color-brown">
      {{ section.title }}
    </div>
    <TransitionGroup name="list">
      <div v-for="item in section.notifs" :key="item.id" class="INTFCenter-section-notification-container">
        <!-- Will show notificationItem or NotificationDeleted if a deletion is performed to rollback -->
        <Transition name="notification">
          <component
            :is="components[getNotificationById(item.id).deleted ? 'NotificationDeleted' : 'NotificationItem']"
            :id="item.id"
            :key="item.id"
            :notification="getNotificationById(item.id)"
            @cancel-deletion="(id) => cancelDeletion(id)"
          />
        </Transition>
      </div>
    </TransitionGroup>
  </div>
</template>
