<script setup>
import useTranslations from '~/composables/useTranslations';
import useFilterStore from '~/stores/filter';
import useNotificationsStore from '~/stores/notifications';

const { $i18n } = useTranslations();
const store = useNotificationsStore();
const filterStore = useFilterStore();
</script>

<template>
  <div class="INTFCenter-notif-center-unread-counter">
    <!-- no filter applied -->
    <div v-if="!filterStore.applied">
      <strong v-if="store.unreadTotal > 1" class="INTFCenter-color-brown">
        {{ store.unreadTotal }} {{ $i18n('unreadNotifications') }}</strong
      >
      <strong v-else-if="store.unreadTotal === 1" class="INTFCenter-color-brown"
        >{{ store.unreadTotal }} {{ $i18n('oneUnreadNotification') }}</strong
      >
    </div>
    <!-- filter is applied -->
    <div v-else>
      <strong v-if="store.filterTotal" class="INTFCenter-color-3ds">
        {{ store.filterTotal }} {{ $i18n('foundNotifications') }}</strong
      >
      <strong v-else-if="store.filterTotal === 1" class="INTFCenter-color-3ds"
        >{{ store.filterTotal }} {{ $i18n('oneFoundNotification') }}</strong
      >
    </div>
    <div v-if="store.filterTotal === 0 || store.unreadTotal === 0"></div>
  </div>
</template>
