<script setup>
import { useNotificationMenu } from '~/composables/notification/useNotificationMenu';
import { Notification} from '~/models/notification';
const props = defineProps({
  isRead: false,
  isStarred: false,
  notification: Notification
});

const notificationMenu = useNotificationMenu();

</script>

<template>
  <div class="INTFCenter-notif-status">
    <span v-if="isRead && !isStarred" 
    :class="'INTFCenter-read-unread INTFCenter-notification-read'" class="cursor-pointer" @click="notificationMenu.setNotificationRead(props.notification)"></span>
    <span v-if="!isRead && !isStarred" 
    :class="'INTFCenter-read-unread INTFCenter-notification-unread'" class="cursor-pointer" @click="notificationMenu.setNotificationRead(props.notification)"></span>
    <span v-if="isRead && isStarred" class="INTFCenter-notification-important fonticon fonticon-favorite-off"></span>
    <span
      v-if="!isRead && isStarred"
      class="INTFCenter-notification-important fonticon fonticon-favorite-on font-semibold"
    ></span>
  </div>
</template>
