<script setup>
import NotificationIcon from './NotificationIcon.vue';
import NotificationMenu from './NotificationMenu.vue';
import { computed } from 'vue';
import { Notification } from '~/models/notification';
import useNotificationsStore from '~/stores/notifications';
import NotificationMessage from './NotificationMessage.vue';
import { ref, onMounted } from 'vue';
import { useNotificationActions } from '~/composables/notification/useNotificationActions';
// not destructuring the store to avoid reactivity issue
const store = useNotificationsStore();
const notifBody = ref(null);
const props = defineProps({
  notification: Notification,
  // In notification center or outside of it.
  center: {
    type: Boolean,
    default: true,
  },
  // Is being shown in merge group
  merge: {
    type: Boolean,
    default: false,
  },

  // to show or not show menu button
  menu: { type: Boolean, default: true },
  // whitespace: { type: Boolean, default: false },
});

// return the icon of the notification.
const icon = computed(() => {
  return props.notification.MESSAGE.icon?.src
    ? props.notification.MESSAGE.icon.src
    : './../../resources/en/1/webapps/NotifAlert/assets/icons/notificon.png';
});

onMounted(() => {
  // will attach notification menu to the notification body
  store.setScrollerBody(notifBody.value);
  // update the actioned label if the notification is actioned
  if (props.notification.isActioned) {
    const { updateActionedLabel } = useNotificationActions();
    updateActionedLabel(props.notification, props.notification.ACTION, false);
  }
});
</script>

<template>
  <div ref="notifBody" class="INTFCenter-notifBody">
    <div class="INTFCenter-notifBody-section">
      <!-- Icon & Message -->
      <div :class="{ 'INTFCenter-is-read': notification.isRead }" class="INTFCenter-notifBody-container">
        <NotificationIcon
          :id="notification.ID"
          :merge="merge"
          :is-link="notification.isIconLink /** need to check notification.isIconLink */"
          :group="notification.isGroup"
          :icon="icon"
          :type="notification.MESSAGE.icon.type"
        />
        <NotificationMessage :notification="notification" :merge="merge" />
      </div>
      <!-- menu button -->
      <NotificationMenu v-if="menu" :is-read="notification.isRead" :options="notification.getOptions" />
      <!-- todo: add close icon for notif alert -->
    </div>
  </div>
</template>
