<script setup>
import { computed } from 'vue';
import useSettingsStore from '../../../../stores/settings';
import PreferencesHome from './PreferencesHome.vue';
import SettingHomeList from './SettingHomeList.vue';

const components = {
  PreferencesHome,
  SettingHomeList,
};
const store = useSettingsStore();

/**
 * Computes the current setting for the PreferencesSectionItem component.
 *
 * @returns {ComputedRef} The computed current setting.
 */
const currentSetting = computed(() => {
  return store.getSetting(store.currentSetting);
});
//
</script>
<template>
  <Transition name="base-center">
    <component
      :setting="currentSetting"
      :is="components[store.appSettingOpened ? 'SettingHomeList' : 'PreferencesHome']"
    ></component>
  </Transition>
</template>
